___CSS_LOADER_EXPORT___.push([module.id, `ion-content {
  width: 100%;
  height: 100%;
  --notch-inset: 0;
  --color: black;
  --background: white url('app-background.jpg') left top / 100vw 100vh;
  --overflow: hidden; /* prevents page from scrolling */
}

.content {
  position: absolute;
  top: var(--notch-inset);
  left: 0;
  width: 100%;
  height: calc(100% - var(--notch-inset));
  /* first attempt to soften pop-in of spinner */
}
.content .logo {
  width: auto;
  height: 12%;
  margin: 3.45% auto 4% auto;
  display: block;
}
.content .hr2 {
  height: 0.4%;
  background-color: black;
  margin-bottom: 4%;
}
.content h1 {
  position: relative;
  margin-top: 17px;
  margin-bottom: 16px;
}
.content .title {
  font-size: 0.8rem;
  width: 87%;
  text-align: center;
  font-weight: bold;
  margin-bottom: 4%;
  position: relative;
}
.content h1 ion-icon {
  font-size: 1.1em;
  position: absolute;
  right: 0.3em;
  top: -0.27em;
}
.content ion-icon.dimmed {
  color: #a8a8a8;
}
.content .hr3 {
  height: 0.4%;
  background-color: black;
  margin-bottom: 3%;
}
.content .lock-image {
  width: auto;
  height: 48%;
  margin: 0 auto 1% auto;
  display: block;
}
.content ion-button {
  margin: auto;
  --color: white;
  /* needed when connect, disconnect, unlock are ion-buttons
  display: block;
  box-shadow: 0.3rem 0.4rem 3px 1px rgb(40, 40, 40, 30%);
  */
}
.content .connect, .content .unlock {
  height: 10%;
  margin: auto;
  display: block;
  /* needed when connect, disconnect, unlock are ion-buttons
  height: 3.5rem;
  width: max(200px, 65%);
  margin: 0 auto 1.5rem auto;
  */
}
.content .unlock-spinner {
  display: block;
  margin: 5rem auto;
  transform: scale(4.5);
}
.content .unlock-spinner-reveal {
  animation: fadeIn 5s;
  -webkit-animation: fadeIn 5s;
}
@keyframes fadeIn {
  0% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}

#device-pane {
  position: absolute;
  top: 35%;
  max-height: calc(100vh - 2em - 35%);
  width: 66%;
  left: 0;
  right: 0;
  margin: 0 auto;
  /*     max-height: calc(100vh - 2em - var(--device-pane-top));  */
  overflow-y: scroll;
  background-color: #ffffff;
  --color: #3a3939;
  border: 3px solid #808080;
  border-radius: 2px;
  padding: 10px;
  font-size: 1rem;
  text-align: center;
  z-index: 200;
}

ion-card-title {
  font-size: 1rem;
  --color: #282828;
  margin-bottom: 3px;
}

.device-list {
  margin-bottom: 1.4rem;
}
.device-list .item {
  /* break long name into closely spaced lines for general usage */
  line-height: 1.3em;
  margin-bottom: 1.4rem;
}

.scanning {
  padding: 0 0 1.7rem 0;
  text-align: center;
}
.scanning ion-spinner {
  display: inline-block;
  position: relative;
  top: 10px;
  left: 15px;
  --color: #bacad4e0;
}

.connecting {
  --color: #3a3939;
  padding: 20px 0;
  line-height: normal;
}
.connecting .selectedDevice {
  margin-bottom: 2rem;
}
.connecting ion-spinner {
  display: block;
  margin: 10px auto 0 auto;
  left: -5px;
  top: 0px;
}

#keypad-pane {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.test {
  position: absolute;
  top: 24%;
  max-height: 75%;
  width: 100%;
  white-space: pre-wrap;
  overflow-y: scroll;
}

@media only screen and (min-width: 380px) {
  .content h1 {
    margin-top: 20px;
    margin-bottom: 0px;
  }
  .content .title {
    font-size: 1.1rem;
    word-spacing: 0.3em;
  }
  .content h1 ion-icon {
    font-size: 1.5em;
    right: 0.3em;
    top: -0.35em;
  }
  .connect, .unlock {
    font-size: 1.1rem;
  }
  .device-list .item {
    font-size: 1.1rem;
    /* break long name into closely spaced lines for general usage */
    line-height: 1.5em;
    margin-bottom: 0.8em;
  }
  .connecting .selectedDevice {
    font-size: 1.2rem;
  }
}
@media only screen and (min-width: 480px) {
  .content h1 {
    margin-top: 20px;
    margin-bottom: 0px;
  }
  .content .title {
    font-size: 1.3rem;
    width: 90%;
  }
  .content h1 ion-icon {
    font-size: 1.7em;
    right: 0.6em;
    top: -0.3em;
  }
  .connect, .unlock {
    font-size: 1.3rem;
    font-weight: 600;
  }
  ion-card-title {
    margin-top: 2rem;
    /*        margin-bottom: 1.2rem;  */
    font-size: 1.4rem;
    font-weight: bold;
  }
  .device-list {
    margin-top: 1.5rem;
  }
  .device-list .item {
    font-size: 1.4rem;
    /* break long name into closely spaced lines for general usage */
    line-height: 1.5em;
    margin-bottom: 0.8em;
  }
  .scanning {
    padding: 1.5rem 0 2.5rem 0;
    font-size: 1.3rem;
  }
  .scanning ion-spinner {
    top: 0.5rem;
    transform: scale(1.5);
  }
  .connecting .selectedDevice {
    margin: 1rem auto 2rem auto;
    font-size: 1.3rem;
  }
  .connecting ion-spinner {
    transform: scale(1.7);
    margin-top: 5rem;
    margin-bottom: 3rem;
  }
}
/* extra-wide devices */
@media only screen and (min-width: 720px) {
  .content h1 {
    margin-top: 20px;
    margin-bottom: 0px;
  }
  .content .title {
    font-size: 1.7rem;
    width: 90%;
  }
  .content h1 ion-icon {
    font-size: 1.7em;
    right: 0.6em;
    top: -0.18em;
  }
}
/* possible solution to intrusive notch */
@media only screen and (min-height: 640px) {
  /*
      ion-content {
          --padding-top: env(safe-area-inset-top);
          --padding-bottom: env(safe-area-inset-bottom);
          --padding-left: env(safe-area-inset-left);
          --padding-right: env(safe-area-inset-right);
      }
  */
  ion-content {
    --notch-inset: 30px;
  }
}
